.background-badge {
	background: url('http://eskipaper.com/images/mountain-peaks-wallpaper-hd-1.jpg') center no-repeat;
	background-size: cover;
	position: fixed;
	top: 8vh;
	left:0;
	width: 100%;
	height: 100%;
	opacity: .8;
	}

.flex-row-badge {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	}

.badge-box {
	position: relative;
	background-color: white;
	border: 1px black solid;
	padding: 2vh 2vw;
	border-radius: 5px;
	width: 60vw;
	height: 10vh;
	}

.badge-img1,.badge-img2,.badge-img3,.badge-img4,.badge-img5,.badge-img6,.badge-img7,.badge-img8 {
	position: relative;
	margin: 0.5vw;
	}

.badge-img1 {
	background: url('../../images/HTML:CSS/HTML5-gray.png') center no-repeat;
	background-size: contain;
	}

.badge-img2 {
	background: url('../../images/JavaScript/basic-Javascript-Logo-gray.png') center no-repeat;
	background-size: contain;
	}

.badge-img3 {
	background: url('../../images/JavaScript/javascript-gray.png') center no-repeat;
	background-size: contain;
	}

.badge-img4 {
	background: url('../../images/Angular/angularjs-gray.png') center no-repeat;
	background-size: contain;
	}

.badge-img5 {
	background: url('../../images/Node/nodejs-logo-gray.png') center no-repeat;
	background-size: contain;
	}

.badge-img6 {
	background: url('../../images/SQL/sql-database-gray.png') center no-repeat;
	background-size: contain;
	}

.badge-img7 {
	background: url('../../images/project1/stars-icon-gray.png') center no-repeat;
	background-size: contain;
	}

.badge-img8 {
	background: url('../../images/group-project/vip-gray.png') center no-repeat;
	background-size: contain;
	}

.my-badge {
	position: relative;
	margin-top: 10vh;
	margin-bottom: 2vh;
	border: 1px black solid;
	width: 200px;
	height: 4vh;
	border-radius: 3px;
	background-color: white;
	font-size: 22px;
	font-weight: bold;
	padding-top: 0.7%;
	}

/*Badge Info Box*/
.badge1,.badge2,.badge3,.badge4,.badge5,.badge6,.badge7,.badge8 {
	background-color: white;
	border: solid lightgray 1px;
	padding: 2vh;
	}


.badge-text-title {
	width: 18%;
	height: 40%;
	font-size: 16px;
	text-align: center;
	}

.badges-info-box {
	margin: 2vh 1vw 2vh 1vw;
	padding: 3vh 1vw;
	border: 1px black solid;
	position: relative;
	border-radius: 3px;
	background-color: rgba(255, 255, 255, 0.81);
	}

.badge-text {
	float: right;
	width: 78%;
	height: 100%;
	background-color: rgba(211, 211, 211, 0.11);
	padding: 0.2vh 0.2vw;
	font-size: 16px;
	text-align: center;
	}

.little-badge-box {
	position: relative;
	width: 9%;
	height: 80%;
	margin-left: 2vw;
	}

.little-badge-box-alt {
	position: relative;
	width: 9%;
	height: 60%;
	margin-left: 2vw;
	}