.background-log {
	background: url('http://www.clipartkid.com/images/476/mountain-and-forest-blue-green-nature-purple-white-powerpoint-SmX7pC-clipart.png') center no-repeat;
	background-size: cover;
	position: fixed;
	top: 8vh;
