.tooltip {
	border-bottom: 1px dotted #000000;
	color: #000000; outline: none;
	cursor: pointer; text-decoration: none;
	position: relative;
	}

.tooltip span {
	margin-left: -999em;
	position: absolute;
	}

.tooltip:hover span {
	font-family: Calibri, Tahoma, Geneva, sans-serif;
	position: absolute;
	left: 1em;
	top: 2em;
	z-index: 99;
	margin-left: 0;
	width: 250px;
	}

.tooltip:hover img {
	border: 0;
	margin: -10px 0 0 -55px;
	float: left;
	position: absolute;
	}

.tooltip:hover em {
	font-family: Candara, Tahoma, Geneva, sans-serif;
	font-size: 14px;
	font-weight: bold;
	display: block;
	padding: 0.2em 0 0.6em 0;
	}

.classic { padding: 0.8em 1em; }

.custom { padding: 0.5em 0.8em 0.8em 2em; }

* html a:hover { background: transparent; }

.classic { background: #FFFFAA; border: 1px solid #FFAD33; }

.critical {
	background: #FFCCAA;
	border: 1px solid #FF3334;
	font-family: 'Roboto', sans-serif;
	font-size: 12px;
	color: black;
	}

.critical-img {
	float: left;
	background: url('../../images/css-tooltips/Critical.png') center no-repeat;
	background-size: contain;
	width: 48px;
	height: 48px;
	}

.help { background: #9FDAEE; border: 1px solid #2BB0D7; }
.info { background: #9FDAEE; border: 1px solid #2BB0D7; }
.warning { background: #FFFFAA; border: 1px solid #FFAD33; }


ul {
	list-style: none;
	padding: 0;
	margin: 0;
	background: white;
	border: 1px solid black;
	}

ul li {
	display: block;
	position: relative;
	float: left;
	}

li ul {
	display: none;
	}

ul li a {
	display: block;
	/*padding: .6em;*/
	padding: 6px 10px;
	text-decoration: none;
	white-space: nowrap;
	color: black;
	font-size: 14px;
	}

ul li a:hover {
	background: #292C2F;
	color: #3BB3E4;
	cursor: pointer;
	}

li:hover > ul {
	display: block;
	position: absolute;
	}

li:hover li { float: none; }

li:hover a {
	background: #F1F1F1;
	}

li:hover li a:hover {
	background: #292C2F; }

.main-navigation li ul li {
	border-top: 0; }

ul ul ul {
	left: 100%;
	top: 0;
	}

ul:before,
ul:after {
	content: " "; /* 1 */
	display: table; /* 2 */
	}

ul:after { clear: both; }